import styled from "styled-components";

export const Container = styled.div`
  display: flex;
  background-color: #001f3f;
  height: 40px;
  align-items: center;
  padding: 0 10px;
  color: white;
  position: fixed;
  width: 100vw;
  z-index: 100;

  p {
    margin: 0 5px;
    cursor: pointer;
  }
`;
