import { en, es } from "../../Localization/language";
import { Wrapper } from "./Title.styles";
import { WorkingOn } from "../WorkingOn/WorkingOn";
import ps5 from "../../Assets/images/ps5.jpg";

type Props = {
  language: string;
};

export const Title: React.FC<Props> = ({ language }) => {
  return (
    <Wrapper id="home" image={ps5}>
      <h1>{language === "es" ? es.title : en.title}</h1>
      <WorkingOn language={language} />
    </Wrapper>
  );
};
